export class UserGroup {
  acl: any[]
  // client_ip: null
  created_at: string
  default_flag: number
  default_service: number
  device_group_list: any[]
  device_list: any[]
  device_list_action: number
  enable: string
  enable_flag: number
  id: number
  ldap_groups: any[]
  manual: string
  // manual_beginning: null
  message: string
  name: string
  // priv-lvl: -1
  // server_ip: null
  service: any[]
  updated_at: string
  valid_from: any;
  valid_until: any;

  empty(): void {
    this.acl = []
    // client_ip: null
    this.created_at = undefined
    this.default_flag = undefined
    this.default_service = 1
    this.device_group_list = []
    this.device_list = []
    this.device_list_action = undefined
    this.enable = undefined
    this.enable_flag = 1
    this.id = undefined
    this.ldap_groups = []
    this.manual = undefined
    // manual_beginning: null
    this.message = undefined
    this.name = undefined
    // priv-lvl: -1
    // server_ip: null
    this.service = []
    this.updated_at = undefined
    this.valid_from = undefined
    this.valid_until = undefined
  }

}
