import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-tac-user-group-form',
  templateUrl: './tac-user-group-form.component.html',
  styleUrls: ['./tac-user-group-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TacUserGroupFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  list = {
    ldap_groups: {
      multiple: true,
      title: 'LDAP Group',
      title_sidebar: 'LDAP Group List',
      search: true,
      apiurl: 'api/mavis/ldap/group/list/',
    },
    acl: {
      multiple: false,
      title: 'Access Control List',
      title_sidebar: 'ACL List',
      search: true,
      apiurl: 'api/tacacs/acl/list/',
    },
    service: {
      multiple: true,
      title: 'Service',
      title_sidebar: 'Service List',
      search: true,
      apiurl: 'api/tacacs/service/list/',
    },
    device_list: {
      multiple: true,
      title: 'Device List',
      title_sidebar: 'Device List',
      search: true,
      apiurl: 'api/tacacs/device/list/',
    },
    device_group_list: {
      multiple: true,
      title: 'Device Group List',
      title_sidebar: 'Device Group List',
      search: true,
      apiurl: 'api/tacacs/device/group/list/',
    }
  }


  constructor(private toastr: ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  setSevice(data) {
    // console.log(data)
    this.options.data.service = data
  }
  setAcl(data) {
    // console.log(data)
    this.options.data.acl = data
  }
  setDeviceList(data) {
    // console.log(data)
    this.options.data.device_list = data
  }
  setDevGroupList(data) {
    // console.log(data)
    this.options.data.device_group_list = data
  }
  setLdap(data) {
    // console.log(data)
    this.options.data.ldap_groups = data
  }

}
