import { Component, OnInit, ViewEncapsulation } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model UserGroup
import { UserGroup } from '../../../../partials/layout/tacgui/_forms/tac-user-group-form/user-group.model'
//Service//
import { UserGroupService } from '../user-group.service'

@Component({
  selector: 'kt-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AddComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new UserGroup,
    old_data: new UserGroup
  }

  constructor(
    private toastr:ToastrService,
    private usrGrp_service: UserGroupService,
    private router:Router,
    private route: ActivatedRoute
  ) {}


  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  add(){
    // this.loadingForm.next(true)
    console.log(this.formParameters.data)

    let data = this.makeClone(this.formParameters.data)
    console.log(data)
    data.service = (data.service[0] && data.service[0].id) ? data.service.map( x => x.id) : null
    data.acl = (data.acl[0] && data.acl[0].id) ? data.acl[0].id : null
    data.device_list = (data.device_list[0] && data.device_list[0].id) ? data.device_list.map( x => x.id) : null
    data.device_group_list = (data.device_group_list[0] && data.device_group_list[0].id) ? data.device_group_list.map( x => x.id) : null
    data.ldap_groups = (data.ldap_groups[0] && data.ldap_groups[0].id) ? data.ldap_groups.map( x => x.id) : null

    if (data.valid_from && data.valid_from.year){
      data.valid_from = data.valid_from.year+'-'+data.valid_from.month+'-'+data.valid_from.day
    }
    if (data.valid_until && data.valid_until.year){
      data.valid_until = data.valid_until.year+'-'+data.valid_until.month+'-'+data.valid_until.day
    }

    // this.toastr.success('Hello world!', 'Toastr fun!');
    this.usrGrp_service.add(data).subscribe(data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if(data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.group) {
        this.toastr.success('User Group Added!');
        this.router.navigate(['../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
